.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  margin-top: 10px;
  margin-left: 10px;
  
}
input{
  height: 25px;
  margin-right: 4px;
  border-radius: 6px;
  border: 2px solid rgb(4, 193, 178);
}
button{
 border-radius: 4px;
  padding: 5px 5px;
  border: 2px solid rgb(4, 193, 178);
}
